.setup {
  width: 50%;
  height: 100%;
  background-color: #e2e2e2;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
}

.range {
  width: 50%;
  height: 10px;
  -webkit-appearance: none;
  border-radius: 10px;
  background-color: #333;
  border: solid 1px #fff;
}

.range::-webkit-slider-thumb {
  width: 15px;
  height: 15px;
  -webkit-appearance: none;
  background-color: #999;
  border-radius: 50%;
  outline: solid 2px #333;
}

.wrapper {
  width: 20%;
  height: calc(10% + 23px);
  margin-top: 10px;
  text-align: center;
  background-color: #000;
}
.wrapper p {
  max-height: 23px;
  padding-bottom: 5px;
  background-color: #e2e2e2;
}

.checkbox input {
  display: none;
}
.checkbox div {
  width: 37.5%;
  height: calc(100% - 23px - 10px);
  margin: 5px;
  background-color: #fff;
  cursor: pointer;
}

.screen {
  width: 70%;
  height: 10%;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #fff;
  font-size: 1.2em;
}

@media screen and (max-width: 850px) {
  .setup {
    width: 100%;
    height: 50%;
  }
}

